<ol>
<li>
<p><strong>合理使用反射</strong></p>
<ul>
<li>优先使用普通方法调用</li>
<li>只在必要时使用反射</li>
<li>缓存反射获取的对象</li>
</ul>
</li>
<li>
<p><strong>注解使用规范</strong></p>
<ul>
<li>注解命名要清晰</li>
<li>注解属性要有默认值</li>
<li>注解要有明确的用途</li>
</ul>
</li>
<li>
<p><strong>安全性考虑</strong></p>
<ul>
<li>反射可能破坏封装性</li>
<li>注意访问权限控制</li>
<li>防止反射攻击</li>
</ul>
</li>
</ol>
<h2 id="常见问题" tabindex="-1"><a class="header-anchor" href="#常见问题"><span>常见问题</span></a></h2>
<ol>
<li>
<p><strong>反射性能问题</strong></p>
<ul>
<li>反射调用比普通调用慢</li>
<li>需要缓存反射对象</li>
<li>避免频繁使用反射</li>
</ul>
</li>
<li>
<p><strong>注解使用问题</strong></p>
<ul>
<li>注解属性类型有限</li>
<li>注解不能继承</li>
<li>注解不能包含方法</li>
</ul>
</li>
<li>
<p><strong>安全性问题</strong></p>
<ul>
<li>反射可以访问私有成员</li>
<li>反射可以修改 final 字段</li>
<li>反射可以调用私有方法</li>
</ul>
</li>
</ol>
